.App {
  text-align: center;
  background-color:#E4F6F5;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 5em;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-items: baseline;
}

.App-header-title {
  color: #ffc107; 
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  font-weight: bold;
  font-size: 3em;
}

.alarm-on{
  height: 2em;
  background-color: crimson;
  transition: 1s linear;
}

.alarm-off{
  height: 0px;
  background-color: lightslategray;
  transition: 1s linear;
}


.App-link {
  color: #61dafb;
}

.leaflet-container {
  height: 800px;
  width: 100%;
}

.Body {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 100%;
}

.RouterPanel{
  width: 400px;
  background-color:#E4F6F5;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.Show{
  width: 10em;
  background-color:#E4F6F5;
  transition: 1s linear;
}

.Hide{
  width: 0px;
  max-width: 0px;
  transition: 1s linear;
}